          <nav class="col header-nav flex items-center z-ml-md">
            <template v-for="route in routes">
              <router-link :class="{ active: $route.matched.some((r) => r.path === route.path) }" :key="route.path" :to="{ path: route.path }" v-if="!route.roles || hasAnyPermission(route.roles)">{{
                route.name
