<script>
const CryptoJS = require('crypto-js')

export default {
  name: 'DefaultLayout',
  data() {
    return {
      routes: [
        {
          name: '网站首页',
          path: '/',
        },
        {
          name: '全部资源',
          path: '/resource',
        },
        {
          name: '实训平台',
          path: '/training',
        },
      ],
      showLoginPanel: false,
    }
  },
  computed: {
    user: (vm) => vm.$store.state.user.user,
  },
  methods: {
    onMenuSelect({ item, key, selectedKeys }) {
      console.log({ item, key, selectedKeys })
      let vm = this
      if (key === 'exit') {
        location.href = vm.$LOGIN_URL + '?t=' + vm.user.token
      } else if (key === 'pwd') {
        this.showPwdModal = true
      }
    },
    handleSubmit(e) {
      let vm = this
      this.$message.destroy()
      if (this.oldpwd === '') {
        this.$message.warning('请输入原密码')
        return
      }
      if (this.newpwd1 === '') {
        this.$message.warning('请输入新密码')
        return
      }
      if (this.newpwd2 === '') {
        this.$message.warning('请输入确认密码')
        return
      }
      if (this.newpwd1 !== this.newpwd2) {
        this.$message.warning('新密码与确认密码不一致')
        return
      }
      this.confirmUpdating = true
      let userName = this.user.username
      let oldPassword = this.oldpwd
      let newPassword = this.newpwd1
      // let oldpwd = CryptoJS.MD5(this.user.username + this.oldpwd).toString()
      // let newpwd = CryptoJS.MD5(this.user.username + this.newpwd1).toString()
      this.$http2
        .post('/system/user/changePassword', {
          userName: 'test2',
          oldPassword,
          newPassword,
        })
        .then(({ data }) => {
          this.confirmUpdating = false
          if (data.success) {
            this.showPwdModal = false
            this.$success({
              title: '提示',
              content: '密码修改成功，请重新登录。',
              onOk: () => {
                this.$store.dispatch('logout')
                location.href = vm.$LOGIN_URL
              },
            })
          } else {
            this.$message.warning(data.msg)
          }
        })
        .catch(() => {
          this.$message.destroy()
          this.$message.warning('服务器响应异常')
          this.confirmUpdating = false
        })
    },
  },
  mounted() {
    // let headerRect = this.$refs.header.$el.getBoundingClientRect()
    // let footerRect = this.$refs.footer.$el.getBoundingClientRect()
    // this.$refs.content.$el.style.minHeight = `calc(100vh - ${headerRect.height + footerRect.height}px)`
  },
}
</script>
