import Vue from 'vue'
import VueRouter from 'vue-router'
import DefaultLayout from '../layout/default.vue'
import { message } from 'ant-design-vue'
const RouterView = {
  template: '<router-view></router-view>',
}

Vue.use(VueRouter)

window.chkVersion = (version) => {
  let _qypt_version = localStorage._qypt_version || window._qypt_version
  window._qypt_version = version
  if (_qypt_version !== version) {
    localStorage._qypt_version = version
    message.loading('系统更新', 1, () => {
      // 刷新页面
      location.reload(true)
    })
  }
}

const compareVersion = function () {
  if (!window._qypt_version) {
    var versionScript = document.createElement('script')
    versionScript.src = 'v.js?v=' + Date.now()
    var s = document.getElementsByTagName('script')[0]
    s.parentNode.insertBefore(versionScript, s)
  }
}

const routes = [
  {
    path: '/',
    name: 'Default',
    component: DefaultLayout,
    children: [
      {
        path: '',
        name: 'Home',
        component: () => import('../views/Home.vue'),
      },
      {
        path: 'login',
        name: 'Login',
        component: () => import('../views/usercenter/login.vue'),
      },
      {
        path: 'register',
        name: 'Register',
        component: () => import('../views/usercenter/register.vue'),
      },
      {
        path: 'forget',
        name: 'Forget',
        component: () => import('../views/usercenter/forget.vue'),
      },
      {
        name: 'resource',
        path: '/resource',
        component: RouterView,
        children: [
          {
            path: '',
            redirect: '/resource/list',
          },
          {
            path: 'list',
            name: 'resource-List',
            component: () => import('../views/resource/list.vue'),
          },
          {
            path: 'detail',
            name: 'resource-detail',
            component: () => import('../views/resource/detail.vue'),
          },
        ],
      },
      {
        name: 'training',
        path: '/training',
        component: () => import('../views/training/index.vue'),
        children: [
          {
            path: '',
            redirect: '/training/task',
          },
          {
            path: 'task',
            name: 'train-task',
            component: () => import('../views/training/task.vue'),
          },
          {
            path: 'answer',
            name: 'training-answer',
            component: () => import('../views/training/answer.vue'),
          },
          {
            path: 'user',
            name: 'training-user',
            component: () => import('../views/training/user.vue'),
          },
        ],
      },
      {
        path: '/training/task/build',
        name: 'training-task-build',
        component: () => import('../views/training/task-build.vue'),
      },
      {
        path: '/training/task/edit',
        name: 'training-task-edit',
        component: () => import('../views/training/task-edit.vue'),
      },
      {
        path: '/training/task/answer',
        name: 'training-task-answer',
        component: () => import('../views/training/task-answer.vue'),
      },
      {
        name: 'usercenter',
        path: 'usercenter',
        component: () => import('../views/usercenter/index.vue'),
        children: [
          {
            path: '',
            redirect: '/usercenter/profile',
          },
          {
            path: 'profile',
            name: 'user-profile',
            component: () => import('../views/usercenter/profile.vue'),
          },
          {
            path: 'pwd',
            name: 'user-pwd',
            component: () => import('../views/usercenter/pwd.vue'),
          },
          {
            path: 'material',
            name: 'user-material',
            component: () => import('../views/usercenter/material.vue'),
          },
          {
            path: 'build',
            component: RouterView,
            name: 'user-build',
            children: [
              {
                path: '',
                redirect: '/usercenter/build/list',
              },
              {
                path: 'list',
                name: 'user-pwd',
                component: () => import('../views/usercenter/build.vue'),
              },
              {
                path: 'detail',
                name: 'user-pwd',
                component: () => import('../views/usercenter/build-detail.vue'),
              },
            ],
          },
        ],
      },
    ],
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
]

const router = new VueRouter({
  routes,
})

router.beforeEach((to, from, next) => {
  compareVersion()
  window.scrollTo(0, 0)
  next()
})
export default router
